<template>
  <div>
    <p class="red">
      温馨提醒:以下关乎您孩子的个人信息,请务必如实填写,带有红色标记的均为必填项,谢谢合作
    </p>
    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="100px"
      style="width:400px"
    >
      <el-form-item
        prop="name"
        label="学生姓名"
      >
        <el-input
          v-model="ruleForm2.name"
          class="inputWidth"
          placeholder="请输入学生姓名"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="pass"
        label="学生性别"
      >
        <el-radio
          v-model="ruleForm2.sex"
          label="1"
        >
          男
        </el-radio>
        <el-radio
          v-model="ruleForm2.sex"
          label="2"
        >
          女
        </el-radio>
      </el-form-item>
      <el-form-item
        prop="idNum"
        label="身份证号"
      >
        <el-input
          v-model="ruleForm2.idNum"
          placeholder="请输入身份证号"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="school"
        label="在读学校"
      >
        <el-input
          v-model="ruleForm2.school"
          placeholder="请输入在读学校"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="address"
        label="家庭住址"
      >
        <el-input
          v-model="ruleForm2.address"
          placeholder="请输入家庭住址"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="code"
        label="邮编"
      >
        <el-input
          v-model="ruleForm2.code"
          placeholder="请输入邮编"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="familyName"
        label="家长姓名"
      >
        <el-input
          v-model="ruleForm2.familyName"
          placeholder="请输入家长姓名"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="familyTel"
        label="联系方式"
      >
        <el-input
          disabled
          v-model="ruleForm2.familyTel"
          placeholder="请输入家长联系方式"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="spareName"
        label="备用联系人"
      >
        <el-input
          v-model="ruleForm2.spareName"
          placeholder="请输入备用联系人"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="spareTel"
        label="联系方式"
      >
        <el-input
          v-model="ruleForm2.spareTel"
          placeholder="请输入备用联系人联系方式"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        label="工作单位"
      >
        <el-input
          v-model="ruleForm2.unit"
          placeholder="请输入工作单位"
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          class="submit"
          type="primary"
          @click="submitForm('ruleForm2')"
        >
          新增学生
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    // 学生姓名
    const validateName = (rule, value, callback) => {
      const myreg = /^[\u0391-\uFFE5]+$/;
      if (value === '') {
        callback(new Error('请输入学生姓名'));
      } else if (!myreg.test(value)) {
        callback(new Error('请输入中文姓名'));
      } else {
        callback();
      }
    };
    // 身份证号
    const validateidNum = (rule, value, callback) => {
      const myreg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (value === '') {
        callback(new Error('请输入身份证号'));
      } else if (!myreg.test(value)) {
        callback(new Error('请输入15位或18位的身份证号'));
      } else {
        callback();
      }
    };
    // 学校
    const validateSchool = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入学校名称'));
      } else {
        callback();
      }
    };
    // 家庭住址
    const validateAddress = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入家庭住址'));
      } else {
        callback();
      }
    };
    // 邮编
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入邮编'));
      } else {
        callback();
      }
    };
    // 家长姓名
    const validateFamilyName = (rule, value, callback) => {
      const myreg = /^[\u0391-\uFFE5]+$/;
      if (value === '') {
        callback(new Error('请输入家长姓名'));
      } else if (!myreg.test(value)) {
        callback(new Error('请输入中文姓名'));
      } else {
        callback();
      }
    };
    // 家长联系方式
    const validateFamilyTel = (rule, value, callback) => {
      const myreg = /^[1][0-9]{10}$/;
      if (value === '') {
        callback(new Error('请输入家长联系方式'));
      } else if (!myreg.test(value)) {
        callback(new Error('联系方式有误'));
      } else {
        callback();
      }
    };
    // 备用联系人
    const validateSpareName = (rule, value, callback) => {
      const myreg = /^[\u0391-\uFFE5]+$/;
      if (value === '') {
        callback(new Error('请输入备用联系人'));
      } else if (!myreg.test(value)) {
        callback(new Error('请输入中文姓名'));
      } else {
        callback();
      }
    };
    // 备用联系方式
    const validateSpareTel = (rule, value, callback) => {
      const myreg = /^[1][0-9]{10}$/;
      if (value === '') {
        callback(new Error('请输入备用联系人联系方式'));
      } else if (!myreg.test(value)) {
        callback(new Error('联系方式有误'));
      } else {
        callback();
      }
    };
    return {
      ruleForm2: {
        userId: '',
        addShow: true,
        name: '',
        sex: '1',
        idNum: '',
        school: '',
        address: '',
        code: '',
        familyName: '',
        familyTel: '',
        spareName: '',
        spareTel: '',
        unit: '',
      },
      rules2: {
        name: [
          { required: true, validator: validateName, trigger: 'blur' },
        ],
        idNum: [
          { required: true, validator: validateidNum, trigger: 'blur' },
        ],
        school: [
          { required: true, validator: validateSchool, trigger: 'blur' },
        ],
        address: [
          { required: true, validator: validateAddress, trigger: 'blur' },
        ],
        code: [
          { required: true, validator: validateCode, trigger: 'blur' },
        ],
        familyName: [
          { required: true, validator: validateFamilyName, trigger: 'blur' },
        ],
        familyTel: [
          { required: true, validator: validateFamilyTel, trigger: 'blur' },
        ],
        spareName: [
          { required: true, validator: validateSpareName, trigger: 'blur' },
        ],
        spareTel: [
          { required: true, validator: validateSpareTel, trigger: 'blur' },
        ],
      },
    };
  },
  mounted() {
    this.ruleForm2.userId = this.$Storage.get('userId');
    if (this.ruleForm2.userId === '' || this.ruleForm2.userId === undefined) {
      this.$router.push('/login');
    } else {
      this.ruleForm2.familyTel = this.$Storage.get('userTel');
    }
  },
  methods: {
    submitForm(formName) {
      // eslint-disable-next-line consistent-return
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http({
            methods: 'POST',
            url: '/pjsys/add.action',
            params: {
              user_id: this.userId,
              stuName: this.ruleForm2.name,
              sex: this.ruleForm2.sex,
              personalNum: this.ruleForm2.idNum,
              schoolname: this.ruleForm2.school,
              Address: this.ruleForm2.address,
              postcode: this.ruleForm2.code,
              parent: this.ruleForm2.familyName,
              mobilephone: this.ruleForm2.familyTel,
              EmergencyContactName: this.ruleForm2.spareName,
              EmergencyContactPhone: this.ruleForm2.spareTel,
              workaddress: this.ruleForm2.unit,
            },
          })
            .then((res) => {
              if (res.data === 1) {
                this.$message({
                  message: '新增成功',
                  type: 'success',
                });
                this.$router.push('/main/info');
              } else if (res.data === 0) {
                this.$message({
                  message: '身份证号重复',
                  type: 'error',
                });
              } else {
                this.$message({
                  message: '新增失败',
                  type: 'error',
                });
              }
            })
            .catch(() => {
              this.$message({
                message: '新增学生出错',
                type: 'error',
              });
            });
        } else {
          this.$message({
            message: '请按要求填写',
            type: 'error',
          });
          return false;
        }
      });
    },
  },
};
</script>

<style lang="stylus" scoped>
.red
  color red
  padding-bottom 20px
.submit{
  width 100%
}
</style>
