<template>
  <el-form
    ref="form"
    :model="form"
    label-width="80px"
    style="width:280px"
  >
    <el-form-item label="面试学生">
      <el-select
        v-model="form.stuId"
        placeholder="请选择面试学生"
        filterable
        @change="stuChange"
      >
        <el-option
          v-for="item in form.stuOptions"
          :key="item.id"
          :label="item.stuName"
          :value="item.id"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="面试科目">
      <el-select
        v-model="form.value"
        placeholder="请选择面试科目"
        filterable
        @change="change"
      >
        <el-option
          v-for="item in form.options"
          :key="item.id"
          :label="item.interviewSubject"
          :value="item.id"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="面试时间">
      <el-select
        v-model="form.time"
        placeholder="请选择面试时间"
        filterable
        @change="change"
      >
        <el-option
          v-for="item in form.timeOptions"
          :key="item.id"
          :label="`${item.starttime}---${item.end_time}`"
          :value="item.id"
        />
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button
        type="primary"
        @click="onBack"
      >
        取消
      </el-button>
      <el-button
        type="primary"
        @click="onSubmit(form)"
      >
        预约
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      form: {
        stuId: '',
        stuOptions: '',
        time: '',
        value: '',
        options: [],
        timeOptions: [],
      },
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else {
      this.getStu();
    }
    this.getInterviewSubject();
  },
  methods: {
    // 获取面试学生
    getStu() {
      this.$http({
        methods: 'POST',
        url: '/TeachingSystem/findById.action',
        params: {
          user_id: this.userId,
          page: 1,
        },
      })
        .then((res) => {
          this.form.stuOptions = res.data;
        })
        .catch(() => {
          this.$message({
            message: '失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 获取面试科目
    getInterviewSubject() {
      this.$http({
        method: 'POST',
        url: 'findAllInterview.action',
      })
        .then((res) => {
          this.form.options = res.data;
        });
    },
    change() {
      this.getTime(this.form.value);
    },
    // 学生改变
    stuChange() {
      // console.log(this.form.stuId);
    },
    //
    // 预约
    onSubmit(formName) {
      if (formName.time === '') {
        this.info('请选择面试时间', 'error');
      } else if (formName.stuId === '') {
        this.info('请选择面试学生', 'error');
      } else {
        this.reserve();
      }
    },
    // 预约面试
    reserve() {
      this.$http({
        method: 'POST',
        url: 'pjsys/selectInterview.action',
        params: {
          stu_id: this.form.stuId,
          interview_timeslot_id: this.form.time,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.info('预约面试成功,您可以在兴趣小组下的预约记录中查看预约详情与预约结果', 'success');
            this.$router.push('/main/reservation');
          }
        });
    },
    // 取消预约
    onBack() {
      this.$router.go(-1);
    },
    // 获取面试时间
    getTime(id) {
      this.$http({
        method: 'POST',
        url: 'findBySubjectInterview.action',
        params: {
          interview_id: id,
        },
      })
        .then((res) => {
          this.form.timeOptions = res.data;
        });
    },
    // yueyue
    //      stu_id  1095
    // interview_timeslot_id   33
    // 提示
    info(message, type) {
      this.$message({
        message,
        type,
      });
    },
  },
};
</script>

