<template>
  <el-table
    :data="tableData"
    style="width: 100%"
  >
    <el-table-column
      align="center"
      prop="student.stuName"
      label="缺勤学生"
      width="180"
    />
    <el-table-column
      align="center"
      prop="attence_time"
      label="缺勤日期"
      width="180"
    />
    <el-table-column
      align="center"
      prop="reasons.reason"
      label="缺勤原因"
    />
  </el-table>
</template>

<script>
export default {
  data() {
    return {
      userId: '',
      tableData: [],
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else { this.getList(); }
  },
  methods: {
    getList() {
      this.$http({
        methods: 'POST',
        url: '/UnAttence.action',
        params: {
          user_id: this.userId,
        },
      })
        .then((res) => {
          // eslint-disable-next-line no-console
          console.log(res.data);
          this.tableData = res.data;
        })
        .catch(() => {
          this.$message({
            message: '获取信息失败,稍后重试',
            type: 'error',
          });
        });
    },
  },
};
</script>

