<template>
  <div class="box">
    <el-card
      class="box-card"
      v-for="o in notice"
      :key="o[0]"
    >
      <div
        slot="header"
        class="clearfix"
      >
        <span>{{ o[1] }}</span>
      </div>
      <div
        class="text item"
        v-html="o[2]"
      />
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      notice: [],
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getNew();
    }
  },
  methods: {
    getNew() {
      this.$http({
        method: 'POST',
        url: '/queryFirstThreeByImessage',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.notice = res.data;
        })
        .catch(() => {
          this.$message({
            message: '获取出错,稍后重试',
            type: 'error',
          });
        });
    },
  },
};
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box {
    display: flex;
    justify-content: space-around;
  }
  .box-card{width: 320px;}
</style>
