<template>
  <div>
    <el-table
      :data="stuList"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column
        fixed
        show-overflow-tooltip
        prop="student.stuName"
        label="学生姓名"
        align="center"
        width="100"
      />
      <el-table-column
        show-overflow-tooltip
        prop="classinfo.subject.subject"
        align="center"
        width="100"
        label="科目"
      />
      <el-table-column
        show-overflow-tooltip
        prop="classinfo.classname"
        align="center"
        width="140"
        label="班级"
      />
      <el-table-column
        show-overflow-tooltip
        width="180"
        align="center"
        label="上课时间"
      >
        <template scope="scope">
          <span v-if="scope.row.classinfo.classroominfo.day===1">
            星期一
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===2">
            星期二
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===3">
            星期三
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===4">
            星期四
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===5">
            星期五
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===6">
            星期六
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===7">
            星期日
          </span>
          {{ scope.row.classinfo.classroominfo.start }}---
          {{ scope.row.classinfo.classroominfo.jieshu }}
        </template>
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        width="60"
        prop="classinfo.keshi"
        align="center"
        label="课时"
      />
      <el-table-column
        show-overflow-tooltip
        width="60"
        prop="classinfo.money"
        align="center"
        label="费用"
      />
      <el-table-column
        show-overflow-tooltip
        width="210"
        label="状态"
        align="center"
        :formatter="formatterColumn"
      />
      <el-table-column
        align="center"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.flag==1"
            size="mini"
            type="primary"
            @click="Payment(scope.row.id,2,'您已申请缴费,请及时到现场财务进行缴费,谢谢配合!')"
          >
            申请缴费
          </el-button>
          <el-button
            v-else-if="scope.row.flag==7"
            size="mini"
            type="primary"
            @click="Payment(scope.row.id,8,'您已申请续费,请及时到现场财务进行续费,谢谢配合!')"
          >
            申请续费
          </el-button>
          <span v-else>
            暂无可进行操作
          </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      stuList: [],
      loading: true,
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getStuList();
    }
  },
  methods: {
    // 获取列表
    getStuList() {
      this.$http({
        methods: 'POST',
        url: '/pisys/findClassinfo.action',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.stuList = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    Payment(id, flag, info) {
      const This = this;
      this.$http({
        method: 'POST',
        url: '/pay.action',
        params: {
          ids: id,
          flag,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            This.info(info, 'success');
            this.getStuList();
          } else {
            This.info('操作失败,请稍后重试!', 'error');
          }
        });
    },
    // 提示消息
    info(message, type) {
      this.$message({
        message,
        type,
      });
    },
    // 根据结果显示不同的提示
    formatterColumn(row) {
      switch (row.flag) {
        case 0:
          return '待审核,请等待';
        case 1:
          return '已报名,未申请缴费';
        case 2:
          return '已申请缴费,请及时到线下缴费';
        case 3:
          return '您已成功缴费';
        case 4:
          return '您已成功退班';
        case 5:
          return '已申请退费,请及时到线下退费';
        case 6:
          return '您已成功退费';
        case 7:
          return '已报名,未申请续费';
        case 8:
          return '已申请续费,请及时到线下续费';
        case 9:
          return '您已成功续费';
        case 10:
          return '您已被拒绝缴费';
        case 11:
          return '您已被拒绝续费';
        case 12:
          return '您已被拒绝退费';
        default:
          return '未知';
      }
    },
  },
};
</script>

