<template>
  <div>
    <el-table
      :data="stuList"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column
        fixed
        show-overflow-tooltip
        prop="stuName"
        label="学生姓名"
        align="center"
        width="100"
      />
      <el-table-column
        label="性别"
        align="center"
        width="70"
      >
        <template scope="scope">
          {{ scope.row.sex === 1 ? '男': '女' }}
        </template>
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="birthday"
        align="center"
        width="100"
        label="生日"
      />
      <el-table-column
        show-overflow-tooltip
        prop="personalNum"
        align="center"
        width="180"
        label="身份证号"
      />
      <el-table-column
        show-overflow-tooltip
        width="180"
        prop="address"
        align="center"
        label="家庭住址"
      />
      <el-table-column
        show-overflow-tooltip
        prop="parent"
        align="center"
        width="100"
        label="家长姓名"
      />
      <el-table-column
        show-overflow-tooltip
        prop="mobilephone"
        align="center"
        width="120"
        label="联系方式"
      />
      <el-table-column
        prop="mobilephone"
        align="center"
        width="180"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            编辑
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      stuList: [],
      loading: true,
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getStuList();
    }
  },
  methods: {
    getStuList() {
      this.$http({
        methods: 'POST',
        url: '/findById.action',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.stuList = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    handleEdit(id) {
      this.$router.push(`/main/upinfo/${id}`);
    },
    handleDelete(id) {
      this.$http({
        methods: 'POST',
        url: '/pjsys/delete.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '删除成功',
              type: 'success',
            });
            this.getStuList();
          } else {
            this.$message({
              message: '已经产生课程记录无法删除!',
              type: 'error',
            });
          }
        })
        .catch(() => {
          this.$message({
            message: '删除失败,请稍后重试',
            type: 'error',
          });
        });
    },
  },
};
</script>

