<template>
  <div>
    <el-table
      :data="stuList"
      style="width: 100%"
      v-loading="loading"
      :row-class-name="tableRowClassName"
    >
      <el-table-column
        fixed
        show-overflow-tooltip
        prop="student.stuName"
        label="学生姓名"
        align="center"
        width="100"
      />
      <el-table-column
        show-overflow-tooltip
        prop="interview_timeslot.interview.interviewSubject"
        align="center"
        width="100"
        label="科目"
      />
      <el-table-column
        show-overflow-tooltip
        prop="interview_timeslot.starttime"
        align="center"
        width="240"
        label="开始时间"
      />
      <el-table-column
        show-overflow-tooltip
        width="240"
        prop="interview_timeslot.end_time"
        align="center"
        label="结束时间"
      />
      <el-table-column
        label="面试结果"
        align="center"
        :formatter="formatterColumn"
      >
        <!-- <template scope="scope">
          {{ scope.row.sex === 1 ? '男': '女' }}
        </template> -->
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      stuList: [],
      loading: true,
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getStuList();
    }
  },
  methods: {
    getStuList() {
      this.$http({
        methods: 'POST',
        url: '/pisys/findInterviewList',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.stuList = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 根据结果显示不同的颜色
    tableRowClassName() {
    // tableRowClassName({ row }) {
      // if (row.result === 1) {
      //   return 'success';
      // }
      // if (row.result === 2) {
      //   return 'err';
      // }
      return '';
    },
    // 根据结果显示不同的提示
    formatterColumn(row) {
      switch (row.result) {
        case 0:
          return '待审核,请等待';
        case 1:
          return '恭喜您,面试通过';
        case 2:
          return '很遗憾,面试未通过';
        default:
          return '未知';
      }
    },
  },
};
</script>

