// 学习日历
<template>
  <img
    :src="img"
    alt="学习日历"
    style="width: 100%"
  >
</template>

<script>
export default {
  data() {
    return {
      img: '',
    };
  },
  mounted() {
    this.$http({
      method: 'POST',
      url: '/firstOnePicture',
    })
      .then((res) => {
        this.img = res.data.picturepath;
      });
  },
};
</script>

