// 班级消息

<template>
  <div>
    <div>
      <span>选择学生：</span>
      <el-select
        v-model="value"
        placeholder="请选择学生"
        filterable
        @change="change"
      >
        <el-option
          v-for="item in options"
          :key="item.id"
          :label="item.stuName"
          :value="item.id"
        />
      </el-select>
    </div>
    <el-table
      v-loading="loading"
      :data="tableData"
      style="width: 100%;margin-top: 20px"
    >
      <el-table-column
        align="center"
        prop="head"
        label="标题"
        width="180"
      />
      <el-table-column
        align="center"
        label="内容"
        prop="message"
      >
        <template
          slot-scope="scope"
        >
          <span
            v-html="scope.row.message"
          />
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="time"
        width="180"
        label="日期"
      />
      <el-table-column
        align="center"
        width="240"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            查看详情
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="page"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="handleCurrentChange"
    />
    <!-- 消息详情 -->
    <el-dialog
      :title="header"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <span v-html="message " />
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userId: '',
      options: [],
      value: '',
      loading: true,
      tableData: [],
      page: 1,
      pages: 1,
      header: '',
      dialogVisible: false,
      message: '',
      total: 1,
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else { this.getStu(); }
  },
  methods: {
    // 获取学生
    getStu() {
      this.$http({
        methods: 'POST',
        url: '/TeachingSystem/findById.action',
        params: {
          user_id: this.userId,
          page: this.pages,
        },
      })
        .then((res) => {
          this.options = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 获取消息
    change(id, page = 1) {
      this.getNotice(id, page);
    },
    // 根据学生获取消息
    getNotice(stuId, page) {
      this.$http({
        methods: 'POST',
        url: 'findimessageByClass.action',
        params: {
          stu_id: stuId,
          page,
        },
      })
        .then((res) => {
          this.tableData = res.data.datas;
          this.total = Number(res.data.total);
        })
        .catch(() => {
          this.$message({
            message: '失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 查看详情
    handleEdit(id) {
      this.dialogVisible = true;
      this.$http({
        methods: 'POST',
        url: 'XQMessageByClass.action',
        params: {
          id,
        },
      })
        .then((res) => {
          this.message = res.data.message;
          this.header = res.data.head;
        });
    },
    // 换页
    handleCurrentChange(page) {
      this.change(this.value, page);
    },
  },
};
</script>

<style scoped>
.page{
  margin-top: 20px;
  float: right
}
</style>
