// 站内消息

<template>
  <div>
    <el-table
      v-loading="loading"
      :data="tableData"
      :row-class-name="tableRowClassName"
      style="width: 100%"
      @select="change"
      @select-all="change"
    >
      <el-table-column
        align="center"
        type="selection"
        width="60"
      />
      <el-table-column
        align="center"
        prop="head"
        label="标题"
        width="180"
      />
      <el-table-column
        align="center"
        label="内容"
        prop="message"
      >
        <template
          slot-scope="scope"
        >
          <span
            v-html="scope.row.message"
          />
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="time"
        width="180"
        label="日期"
      />
      <el-table-column
        align="center"
        width="80"
        label="状态"
      >
        <template scope="scope">
          {{ scope.row.flag === 1 ? '已读': '未读' }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        width="240"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            查看详情
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id)"
          >
            删除消息
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="bottom">
      <el-button
        size="mini"
        type="danger"
        @click="selDelete"
      >
        删除消息
      </el-button>
      <!-- 分页 -->
      <el-pagination
        class="page"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page.sync="currentPage1"
        @current-change="handleCurrentChange"
      />
    </div>

    <!-- 消息详情 -->
    <el-dialog
      :title="header"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <span v-html="message " />
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      // loading效果
      loading: true,
      // 总数居
      total: 1,
      // 详情中得标题头
      header: '',
      // 详情中的内容
      message: '',
      // 当前选择得id
      userId: '',
      // 表格数据
      tableData: [],
      // 当前页
      currentPage1: 2,
      // 多选操作得id
      ids: [],
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else { this.getList(); }
  },
  beforeRouteUpdate(to, from, next) {
    next();
    this.getList();
  },
  methods: {
    tableRowClassName({ row }) {
      if (row.flag === 1) {
        return 'read-row';
      }
      return '';
    },
    getList() {
      this.$http({
        methods: 'POST',
        url: '/findMessage.action',
        params: {
          user_id: this.userId,
          page: this.$route.params.id,
        },
      })
        .then((res) => {
          this.tableData = res.data.datas;
          this.total = res.data.total;
          this.loading = false;
          this.currentPage1 = Number(this.$route.params.id);
        })
        .catch(() => {
          this.$message({
            message: '获取信息失败,稍后重试',
            type: 'error',
          });
        });
    },
    // 消息详情查看
    handleEdit(id) {
      // 查看详情
      this.$http({
        methods: 'POST',
        url: '/XQMessage.action',
        params: {
          id,
        },
      })
        .then((res) => {
          this.dialogVisible = true;
          this.header = res.data.head;
          this.message = res.data.message;
        })
        .catch(() => {
          this.$message({
            message: '获取详情失败，请稍后重试',
            type: 'error',
          });
        });
      // 标记已读
      this.$http({
        methods: 'POST',
        url: '/updateFlagMessage.action',
        params: {
          id,
        },
      })
        .then(() => {
          this.getList();
        })
        .catch(() => {
          this.$message({
            message: '获取详情失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 删除单个消息
    handleDelete(id) {
      this.$http({
        methods: 'POST',
        url: '/deleteMessage.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '删除成功',
              type: 'success',
            });
            this.getList();
          } else {
            this.$message({
              message: '删除失败，请稍后重试',
              type: 'error',
            });
          }
        })
        .catch(() => {
          this.$message({
            message: '获取详情失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 换页
    handleCurrentChange(id) {
      this.$router.push(`/main/all/${id}`);
    },
    // 多选
    change(selection) {
      this.ids = [];
      // eslint-disable-next-line no-plusplus
      for (let i = 0; i < selection.length; i++) {
        this.ids.push(Number(selection[i].id));
      }
    },
    // 多选删除
    selDelete() {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteAll.action',
        data: {
          ids: this.ids,
        },
      })
        .then((res) => {
          // eslint-disable-next-line no-console
          console.log(res.data);
        });
    },
  },
};
</script>

<style lang="stylus" scoped>
.bottom
  margin-top 20px
  .page
    float right
</style>
