<template>
  <div>
    <h2> 当前账户:{{ user }}</h2>
    <el-button
      type="danger"
      @click="quit"
    >
      退出账户
    </el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: '',
    };
  },
  mounted() {
    this.user = this.$Storage.get('userTel');
    if (this.user === '' || this.user === undefined) {
      this.$router.push('/login');
    }
  },
  methods: {
    quit() {
      this.$Storage.clearAll();
      this.$router.push('/login');
    },
  },
};
</script>

