// 班级消息

<template>
  <div>
    <div>
      <span>选择学生：</span>
      <el-select
        v-model="value"
        placeholder="请选择学生"
        filterable
        @change="change"
      >
        <el-option
          v-for="item in options"
          :key="item.id"
          :label="item.stuName"
          :value="item.id"
        />
      </el-select>
      <span>选择学科：</span>
      <el-select
        v-model="subject"
        placeholder="请选择学科"
        filterable
        @change="change"
      >
        <el-option
          v-for="item in subjectOptions"
          :key="item.id"
          :label="item.major"
          :value="item.id"
        />
      </el-select>
    </div>
    <el-table
      v-loading="loading"
      :data="tableData"
      style="width: 100%;margin-top: 20px"
    >
      <el-table-column
        fixed
        align="center"
        prop="subject.subject"
        label="科目"
        width="100"
      />
      <el-table-column
        show-overflow-tooltip
        align="center"
        width="160"
        label="班级"
        prop="classname"
      />
      <el-table-column
        show-overflow-tooltip
        align="center"
        width="180"
        label="适合年龄"
      >
        <template scope="scope">
          {{ new Date(scope.row.min_age).getFullYear()-1 }}年
          {{ new Date(scope.row.min_age).getMonth()+1 }}月
          {{ new Date(scope.row.min_age).getDate() }}日---
          {{ new Date(scope.row.min_age).getFullYear() }}年
          {{ new Date(scope.row.min_age).getMonth()+1 }}月
          {{ new Date(scope.row.min_age).getDate() }}日
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        width="180"
        label="上课时间"
      >
        <template scope="scope">
          <span v-if="scope.row.classroominfo.day===1">
            星期一
          </span>
          <span v-if="scope.row.classroominfo.day===2">
            星期二
          </span>
          <span v-if="scope.row.classroominfo.day===3">
            星期三
          </span>
          <span v-if="scope.row.classroominfo.day===4">
            星期四
          </span>
          <span v-if="scope.row.classroominfo.day===5">
            星期五
          </span>
          <span v-if="scope.row.classroominfo.day===6">
            星期六
          </span>
          <span v-if="scope.row.classroominfo.day===7">
            星期日
          </span>
          {{ scope.row.classroominfo.start }}---
          {{ scope.row.classroominfo.jieshu }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="classroominfo.classRoom.name"
        width="80"
        label="教室"
      />
      <el-table-column
        align="center"
        prop="keshi"
        width="80"
        label="课时"
      />
      <el-table-column
        align="center"
        prop="money"
        width="80"
        label="费用"
      />
      <el-table-column
        align="center"
        width="180"
        label="操作"
      >
        <template slot-scope="scope">
          <!-- {{ scope.row.kaifang }}开放选课 =1
          {{ scope.row.subject.interview }}需要面试 =1
          {{ scope.row.infoFlag }}没有选过该课程 =0
          {{ scope.row.num }}剩余人数>0 -->
          <el-button
            size="mini"
            type="primary"
            v-if="scope.row.kaifang===1 && scope.row.infoFlag ===0 &&scope.row.num >0"
            @click="handleEdit(scope.row.id)"
          >
            选课
          </el-button>
          <el-button
            size="mini"
            type="primary"
            v-if="scope.row.subject.interview === 1&&
              scope.row.kaifang===1 &&
              scope.row.infoFlag ===0 &&
              scope.row.num >0"
            @click="handleInterview"
          >
            预约面试
          </el-button>
          <el-button
            size="mini"
            disabled
            v-if="
              scope.row.kaifang===0 ||
                scope.row.infoFlag ===1 ||
                scope.row.num <=0"
          >
            选课
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div style="margin-top: 20px;cursor:pointer;">
      <span @click="why">
        <i class="el-icon-info" />为什么不能选课?
      </span>
      <el-pagination
        class="page"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handleCurrentChange"
      />
    </div>
    <!-- 确认报名 -->
    <el-dialog
      title="报名提示"
      :visible.sync="confirm"
      width="30%"
    >
      <span>
        确定报名吗?<br>
        具体的选班结果缴费后由老师统一进行安排
      </span>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="confirm = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="confirmApply"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
    <!-- 选课成功提示 -->
    <el-dialog
      title="选课成功提示"
      :visible.sync="success"
      width="30%"
    >
      <p>
        恭喜您选课成功<br>
        请于2019年1月12日、13日前往中心现场缴费，完成报名,
        <span style="color:red">
          逾期不候。
        </span>
        谢谢您的配合！
      </p>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="success=false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>

    <!-- 报名须知 -->
    <m-info />
    <!-- 为什么不能选课 -->
    <el-dialog
      title="为什么不能选课?"
      :visible.sync="Whysel"
      width="50%"
    >
      <div>
        <h2>选课的条件</h2>
        <p>
          1.本班级对应的上课时间与您其他课程时间不冲突
        </p>
        <p>
          2.符合该相应课程的年龄要求
        </p>
        <p>
          3.在开放选课的时间内
        </p>
        <p>
          4.本班级报名剩余人数大于0
        </p>
        <h2>温馨提醒<br></h2>
        <h3 style="color:red">
          选课成功后，请根据提示，安排缴费。否则所选课程可能会失效。
        </h3>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="Whysel = false"
        >
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import MInfo from './Info.vue';

export default {
  data() {
    return {
      // 选课成功
      success: false,
      // 确认选课
      confirm: false,
      // 为什么不能选课
      Whysel: false,
      userId: '',
      options: [],
      value: '',
      subject: '',
      subjectId: '',
      subjectOptions: [],
      loading: true,
      tableData: [],
      page: 1,
      pages: 1,
      header: '',
      message: '',
      total: 1,
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else {
      this.getStu();
      this.getSubject();
    }
  },
  methods: {
    why() {
      this.Whysel = true;
    },
    // 获取学生
    getStu() {
      this.$http({
        methods: 'POST',
        url: '/TeachingSystem/findById.action',
        params: {
          user_id: this.userId,
          page: this.pages,
        },
      })
        .then((res) => {
          this.options = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 获取科目
    getSubject() {
      this.$http({
        methods: 'POST',
        url: '/findAllMajors.action',
        params: {
          user_id: this.userId,
          page: this.pages,
        },
      })
        .then((res) => {
          this.subjectOptions = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 获取消息
    change() {
      this.getNotice(this.value, this.subject);
    },
    // 根据学生获取课程
    getNotice(stuId, major, page = 1) {
      this.$http({
        methods: 'POST',
        url: '/pjsys/findBySubjectAndAge.action',
        params: {
          stu_id: stuId,
          major_id: major,
          page,
        },
      })
        .then((res) => {
          this.tableData = res.data.datas;
          this.total = Number(res.data.total);
        })
        .catch(() => {
          this.$message({
            message: '失败，请稍后重试',
            type: 'error',
          });
        });
    },
    // 选课
    handleEdit(id) {
      this.confirm = true;
      this.subjectId = id;
    },
    // 预约面试
    handleInterview() {
      this.$router.push(`/main/selinterview/${this.value}`);
    },
    // 换页
    handleCurrentChange(page) {
      this.getNotice(this.value, this.subject, page);
    },
    // 提示
    info(message, type) {
      this.$message({
        message,
        type,
      });
    },
    // 确认报名
    confirmApply() {
      this.confirm = false;
      this.$http({
        methods: 'POST',
        url: 'pjsys/selectSubject.action',
        params: {
          stu_id: this.value,
          classinfo_id: this.subjectId,
        },
      })
        .then((res) => {
          if (res.data === 0) {
            this.info('课程时间段有冲突，请选择其他课程。', 'error');
          } else if (res.data === 1) {
            this.success = true;
            this.getNotice(this.value, this.subject);
          } else if (res.data === 2) {
            this.info('你的面试未通过，不能选课！', 'error');
          } else if (res.data === 3) {
            this.info('当前班级名额已报满,请刷新页面获取最新可选课程,并选择其他课程！', 'error');
          } else {
            this.info('出错了,请联系技术人员！', 'error');
          }
        });
    },
  },
  components: {
    MInfo,
  },
};
</script>

<style scoped>
.page{
  float: right
}
</style>
