// 考勤汇总
<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column
        align="center"
        prop="stuName"
        label="出勤学生"
        width="180"
      />
      <el-table-column
        align="center"
        prop="attence.1"
        label="出勤次数(单位次)"
        width="180"
      />
      <el-table-column
        align="center"
        prop="attence.2"
        label="缺勤次数(单位次)"
      />
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userId: '',
      tableData: [],
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else { this.getList(); }
  },
  methods: {
    getList() {
      this.$http({
        methods: 'POST',
        url: '/findAttenceById.action',
        params: {
          user_id: this.userId,
        },
      })
        .then((res) => {
          // eslint-disable-next-line no-console
          console.log(res.data);
          this.tableData = res.data;
        })
        .catch(() => {
          this.$message({
            message: '获取信息失败,稍后重试',
            type: 'error',
          });
        });
    },
  },
};
</script>

